.app {
    background-color: #151516;
    background-size: cover;
    min-height: 100vh;
    height: auto;
}

.pollimg{
    mask-image: linear-gradient(to bottom, rgb(0, 0, 0, 1), rgb(0, 0, 0, 0));
}
.appmain {
    color: white;
    padding-top: 75px;
    padding-left: 130px;
    width: 87%;
    display: flex;
    flex: 1;
    justify-content: space-evenly;
}

.appleft {
    width: 65%;
}

.appright {
    width: 30%;
}

.wrapper-main {
    position: fixed;
    /* top: 0; */
    height: 90vh;
    width: 30vw;
    overflow-y: auto;
}

.wrapper-main::-webkit-scrollbar {
    width: 5px;
}

.wrapper-main::-webkit-scrollbar-track {
    background: transparent;
}

.wrapper-main::-webkit-scrollbar-thumb {
    background: rgba(55, 55, 55, 1);
    border-radius: 10px;
}

::-webkit-scrollbar {
    width: 5px;
}

::-webkit-scrollbar-track {
    background: black;
}

::-webkit-scrollbar-thumb {
    background: rgba(55, 55, 55, 1);
    border-radius: 10px;
}

